/*---------- menu link animation ----------*/
.nav-link,
.tab-link {
  display: block;
  position: relative;
  transition: color .3s ease;
  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: auto;
    width: 0;
    height: 3px;
    z-index: 1;
  }
  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: auto;
    width: 0;
    height: 3px;
    z-index: 1;
    background-color: #fff;
    transition: all 0.25s cubic-bezier(0.694, 0.048, 0.335, 1) 0.15s;
  }
  &:after {
    background-color: #f97a2e;
    transition: all 0.25s cubic-bezier(0.694, 0.048, 0.335, 1);
  }
  &:hover {
    text-decoration: none;
    &:after, &:before {
      width: 100%;
      left: 0;
    }
  }
}
/*---------- end of menu link animation ----------*/
.header {
    height: 160px;
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    z-index: 10;
    transition: all 0.3s;
}

.list-menu-right {
  max-width: 148px;
  width: 100%;
  display: flex;
  align-items: center;
}

.list-item-right {
  margin-right: 25px;
  font-family: 'Work Sans', sans-serif;
  &:last-child {
    margin-right: 0;
  }
  .wrap-lang {
    display: flex;
    align-items: center;
  }
  .lang {
    margin-left: 8px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
  }
  i {
    font-size: 18px;
    vertical-align: middle;
  }
}

.nav-link,
.list-item-right .lang,
.list-item-right i {
  color: #fff;
  transition: all .3s;
  text-transform: uppercase;
  text-decoration: none;
}

.nav-link:hover,
.list-item-right .lang:hover,
.list-item-right i:hover {
  color: #f97a2e;
  text-decoration: none;
}

.wrap-main-nav {
  width: 100%;
  display: flex;
  font-size: 13px;
  line-height: 23px;
  font-weight: 600;
  font-family: 'Montserrat', sans-serif;
}

.main-nav {
  width: 100vw;
  height: 160px;
  display: grid;
  grid-template-columns: 1fr 3fr;
  transition: all 0.3s;
}

.navShadow .main-nav {
    height: 100px;
}

.header.navShadow {
    background: url(../img/main-page/header/header-bg-mobile.png) no-repeat top center/cover;
    box-shadow: 0 4px 30px -5px rgba(0, 0, 0, 0.5);
    height: 101px;
}

.header.navShadow .word-mark {
  opacity: 0;
}

.brand,
.menu,
.list-menu {
  display: flex;
  align-items: center;
}

