.logo {
  cursor: pointer;
}

.word-mark {}

.menu {
  justify-content: flex-end;
}

.list-menu-item {
  margin-left: 35px;
}

.menu-toggle {
  width: 55px;
  height: 55px;
  background: #9045f8;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  display: none;
}

.menu-toggle:hover .bar {
  width: 25px;
}

.menu-toggle.closeMenu .bar {
  width: 25px;
}

.menu-toggle.closeMenu .bar:first-child {
  -webkit-transform: translateY(7px) rotate(45deg);
          transform: translateY(7px) rotate(45deg);
}

.menu-toggle.closeMenu .bar:nth-child(2) {
  -webkit-transform: scale(0);
          transform: scale(0);
}

.menu-toggle.closeMenu .bar:last-child {
  -webkit-transform: translateY(-7px) rotate(-45deg);
          transform: translateY(-7px) rotate(-45deg);
}

.bar {
  width: 25px;
  height: 2px;
  background: #fff;
  transition: 0.3s ease-in-out;
}

.bar:nth-child(2) {
  width: 20px;
  margin: 5px 0;
}

.bar:last-child {
  width: 15px;
}

@media screen and (max-width: 992px) {
  .main-nav {
    grid-template-columns: 1fr 1fr;
  }
  .list-menu-right {
    margin: 0 auto;
  }
  .menu-toggle {
    display: flex;
  }
  .list-menu {
    display: inline-block;
    // width: 100vw;
    width: 100%;
    height: 0;
    left: 0;
    right: 0;
    background: linear-gradient(135deg, #b73afa 20%, #2362f4 100%);
    // position: absolute;
    position: fixed;
    top: 160px;
    box-shadow: 0 5px 30px -4px rgba(0, 0, 0, 0.2);
    transition: all 0.3s;
  }
  .list-menu.showMenu {
    height: 100vh;
    .list-menu-item {
      opacity: 1;
      visibility: visible;
    }
  }
  .list-menu-item {
    text-align: center;
    margin-left: 0;
    padding: 15px 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s 0.1s;
      &:first-child {
        padding-top: 25px;
      }
  }
  .nav-link:hover {
    &:after, 
    &:before {
      display: none;
    }
  }
}