.our-portfolio {
	padding: 75px 0;
		.tab-container {
			max-width: 100%;
			margin: 0 auto;
		}
		.tab-container .tab-menu {
			padding: 0;
			list-style: none;
			display: flex;
			justify-content: flex-end;
			margin: 0;
			text-transform: uppercase;
			font-weight: 600;
			font-family: 'Montserrat', sans-serif;
			font-size: 13px;
		}

		.tab-menu .tab-menu-item {
			margin-right: 35px;
				&:last-child {
					margin-right: 0;
				}
		}

		.tab-container .tab-menu li .tab-link {
			padding: 10px 0 5px;
			display: inline-block;
			color: #333333;
			text-decoration: navajowhite;
			text-transform: capitalize;
			border-bottom:3px solid transparent;
			outline:none;
			&:hover {
				color: #f97a2e;
			}
		}

		.tabs-container {
			position: relative;
			margin: 0 auto 50px;
			max-width: 1170px;
			width: 100%;
			padding-right: 15px;
    		padding-left: 15px;
		}

		.tab-container .tab-menu li.active-tab .tab-link {
			color: $brand-color;
		}

		.tab-data {
			padding: 0;
			position: relative;
			overflow: hidden;
		}

		.tab-data .data-content {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			padding: 0;
			opacity: 0;
			visibility: hidden;
			bottom: 0;
			transition: 500ms all;
		}

		.tab-data .data-content.active-data {
			opacity: 1;
			visibility: visible;
			position: relative;
		}

		.tab-data-wrap {
			transition: 500ms all;
			transform: translate3d(0,0,0);
		}

		.prev .tab-data-wrap {
			transform: translate3d(-100%,0,0);
		}

		.next .tab-data-wrap {
			transform: translate3d(100%,0,0);
		}

		.active-data .tab-data-wrap {
			transform: translate3d(0,0,0);
		}

		.fluid-no-padding {
			padding: 0;
		}

		.tab-link {
		  transition: color .5s ease;
		  &:before {
		    background-color: $brand-color;
		  }
		  &:after {
		    background-color: #f97a2e;
		  }
		}
}

.sl-overlay {
		background: rgba(0,0,0,0.8);
		opacity: inherit;
	}

	.sl-wrapper .sl-close,
	.sl-wrapper .sl-navigation button,
	.sl-wrapper .sl-counter {
		color: #fff;
	}

	.has-lightbox,
	.has-lightbox body {
		padding-right: 0 !important;
		overflow: hidden !important;
	}

	.sl-wrapper button:hover {
		color: #f97a2e;
		opacity: inherit;
	}

.gallery-item {
	position: relative;
	&:hover .gallery-item-info {
    	// bottom: 0;
    	transform: translateY(-100%);
    }
}

.gallery-item-info {
	box-shadow: 0px 0px 20px 0px rgba(255,255,255,0.5);
	display: flex;
	align-items: center;
	padding: 20px 25px;
	position: absolute;
	width: 100%;
	// bottom: -92px;
	transform: translateY(0);
    background: rgba(178, 192, 255, 0.85);
    transition: all .3s;
}

.item-info-left {
	flex: 1;
}

.item-info-right {
	text-align: right;
}

.awesome,
.llustration {
	margin-bottom: 0;
}

.awesome {
	font-size: 17px;
	font-weight: bold;
	text-transform: capitalize;
	color: #010927;
}

.llustration {
	font-family: 'Work Sans', sans-serif;
	font-size: 15px;
	color: #3a4366;
}

.item-info-right i {
	color: #010927;
	border: 1px solid $brand-color;
	border-radius: 50%;
    width: 45px;
    height: 45px;
    font-size: 14px;
    position: relative;
    &:before {
		position: absolute;
	    top: 50%;
	    left: 50%;
	    transform: translate(-50%, -50%);
	} 
}

