@import "variables";

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,600|Playfair+Display:400,700|Work+Sans:400,600&display=swap');

@font-face {
    font-family: 'Segoe UI Bold';
    src: url('_fonts/SegoeUI-Bold.eot');
    src: url('_fonts/SegoeUI-Bold.eot?#iefix') format('embedded-opentype'),
        url('_fonts/SegoeUI-Bold.woff') format('woff'),
        url('_fonts/SegoeUI-Bold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

// font-family: 'Montserrat', sans-serif;
// font-family: 'Work Sans', sans-serif;
// font-family: 'Playfair Display', serif;
// font-family: 'Segoe UI Bold';

a {
  color: $link;
  transition: color 200ms;
  text-decoration: none;
  &:hover {
    color: darken($link, 20);
  }
}

@each $name, $value in $colorList {
  .has-background-#{$name} {
    background-color: $value;
  }
}

@each $name, $value in $fontColors {
  .has-text-#{$name} {
    color: $value;
  }
}

html, body {
  padding: 0;
  margin: 0;
  max-width: 100%;
  font-family: 'Work Sans', sans-serif;
  font-size: 15px;
  color: #2f2f2f;
  position: relative;
  overflow: hidden;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

ul, li {
  display: block;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

h1 {
    font-size: 76px;
    line-height: 93px;
}

h2 {
    font-size: 60px;
    line-height: 58px;
    // letter-spacing: -4px;
}

h3 {}

h4 {
  font-size: 22px;
  line-height: 42px;
}

h5 {
  font-size: 18px;
  line-height: 42px;
}

h6 {
    font-size: 16px;
    line-height: 42px;
}

h1, h2, h3, h4, h5, h6 {
    color: $title-color;
    font-weight: bold;
    font-family: sans-serif;
    margin: 0;
}

p {
  line-height: 26px;
  margin: 0 0 10px 0;
}

.main {
  padding-top: 160px;
}

.text-center {
  text-align: center; 
}

.overflow-h {
  overflow: hidden !important;
}