.testimonials {
	padding: 75px 0 25px;
}

.testimonials .title-block {
	text-align: center;
	position: relative;
	margin-bottom: 80px;
	&:before {
		content: '';
		background: url('../img/main-page/main/testimonials/title-quotes.png') no-repeat;
		width: 145px;
		height: 104px;	
		position: absolute;
		top: -5px;
		left: 50%;
		transform: translateX(-50%);
	}
}

.wrap-testimonials {
	border-radius: 3px;
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.15);
   	padding: 18px 20px;
    margin: 15px 0;
}

.testimonials-head {
	width: 24px;
	height: 17px;
	background: url('../img/main-page/main/testimonials/quotes-small.png') no-repeat center center;
	margin-bottom: 15px;
}

.testimonials-text {
	font-family: 'Playfair Display', serif;
	font-weight: bold;
	font-size: 15px;
	margin-bottom: 35px;
}

.testimonials-author {
	display: flex;
	align-items: center;
}

.testimonials-photo {
	width: 65px;
	height: 65px;
	border-radius: 50%;
	margin-right: 25px;
	overflow: hidden;
	box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
	align-self: flex-start;
}

.testimonials-wrap-name {
	font-family: 'Montserrat', sans-serif;
	line-height: 42px;
	font-weight: 300;
}

.testimonials-name {
	margin-top: 2px;
}

.testimonial-social {
	padding: 0;
	margin: 0;
	list-style: none;
	display: flex;
}

.social-item {
	margin-right: 10px;
	height: 31px;
}

.social-item:last-child {
	margin-right: 0;
}

.social-item i {
    vertical-align: top;
    font-size: 18px;
    transition: all .3s;  
}

.facebook {
	color: #2a40e5;
}
.basketball {
	color: #ea4c89;
}
.twitter {
	color: #03a9f4;
}