.what-we-do {
	padding-top: 125px;
}

.what-we-do .title-block {
	text-align: center;
}

.what-we-do-wrap {
	text-align: center;
	margin-bottom: 110px;
}

.what-we-do-pic {
	background: no-repeat center center;
	min-height: 145px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 25px;
}

.what-we-do-pic.bg-1 {
	background-image: url('../img/main-page/main/what-we-do/what-we-do-pic-bg.png');
}

.what-we-do-pic.bg-2 {
	background-image: url('../img/main-page/main/what-we-do/what-we-do-pic-bg-2.png');
}

.what-we-do-pic.bg-3 {
	background-image: url('../img/main-page/main/what-we-do/what-we-do-pic-bg-3.png');
}

.what-we-do-pic.bg-4 {
	background-image: url('../img/main-page/main/what-we-do/what-we-do-pic-bg-4.png');
}

.what-we-do-number {
	width: 30px;
	height: 30px;
	font-weight: bold;
	font-size: 14px;
	background: linear-gradient(135deg, rgba(169,46,240,1) 0%, rgba(25,145,237,1) 67%, rgba(25,142,237,1) 100%);
	border-radius: 50%;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(calc(-50% - 52px));
}

.what-we-do-number span {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: $white;
}

.what-we-do-pic i {
	color: $brand-color;
	font-size: 70px;
}

.what-we-do-description {
    line-height: 26px;
}

.what-we-do-wrap.what-we-do-form-wrap {
	text-align: left;
	margin-bottom: 0;
}

.what-we-do-form-wrap {
	max-width: 600px;
	width: 100%;
	margin: 0 auto;
}

.what-we-do-form {
	border-radius: 3px;
	padding: 40px;
	background: url('../img/main-page/main/what-we-do/form-left-bg.png') no-repeat -275px -145px,
				url('../img/main-page/main/what-we-do/form-right-bg.png') no-repeat 365px -228px;
	box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.25);
}

.wrap-subscirbe {
	position: relative;
}

.wrap-subscirbe input[type=email] {
	border: 1px solid #758c92;
	border-radius: 50px;
	padding: 18px 203px 18px 30px;
	width: 100%;
}

.wrap-subscirbe input[type=submit] {
	position: absolute;
	top: 2px;
	right: 2px;
	border: none;
	border-radius: 50px;
	background: $custom-btn-bg;
	color: $white;
	text-transform: uppercase;
	padding: 17px 45px;
	box-shadow: 10px 7px 25px 1px rgba(185,185,185,0.5);
	transition: all .3s;
	&:hover {
		cursor: pointer;
		box-shadow: 4px 4px 9px 0 rgba(185, 185, 185, 1);
	}
}
	