@media screen and (min-width: 1920px) {
	.home:before {
		background: url('../img/main-page/main/main-bg/bg2500.png') no-repeat center top,
    	url('../img/main-page/main/main-bg/header-bg-left.png') no-repeat 0 900px;
	}
}

@media screen and (min-width: 1800px) {
	.wrap-home-offer {
    margin-bottom: 350px;
}
}

@media screen and (min-width: 1200px) {
	.container {
	    max-width: 1170px;
	}
}

/* 992 - 1200 px */
@media screen and (max-width: 1200px) {
	/*---------- home ----------*/
	.home:before {
	    // width: calc(50% + (0.97 * 1170px));
	}
	.wrap-home-offer {
	    margin: 60px 0 0px;
	}
	.wrap-home-offer {
		.homeDescription{
			padding-right: 400px;
		}
	}

	/*---------- .our-services ----------*/
	.reverse-description .title {
	    padding-left: 0;
	}
}

@media screen and (min-width: 993px) {
	/*---------- header ----------*/
	// .logo-mobile {
	// 	display: none;
	// }
	.mobile-home{
		display: none;
	}
}

/* 768 - 992 px */
@media screen and (max-width: 992px) {
	/*---------- base ----------*/
	h1 {
		font-size: 60px;
    	line-height: 1;
	}

	h2,
	.get-started .title-block .title {
		font-size: 45px;
    	line-height: 1;
	}
	.sub-title {
		margin-bottom: 0;
	}

	/*---------- header ----------*/
	.header {
		background: url('../img/main-page/header/header-bg-mobile.png') no-repeat top center /cover;
	}
	.logo-desktop {
		display: none;
	}

	/*---------- home ----------*/
	.home {
		background: linear-gradient(135deg, #b73afa 20%, #2362f4 100%);
	}
	.home:before {
		background: none;
	}
	.wrap-home-offer {
		padding: 80px 0;
		margin: 0

	}
	.wrap-home-offer {
		text-align: center;
		.home-title {
			padding-right: 0;
			margin-bottom: 25px;
			// color: #3a4366;
		}
		.homeDescription{
			padding-right: 0;
			margin-bottom: 25px;
			// color: #3a4366;
		}
	}
	/*---------- .our-services ----------*/
	.wrap-our-services-description {
	    margin-bottom: 0;
	}
	/* .reverse-description .title {
	    padding-left: 0;
	} */
	/*---------- our-portfolio ----------*/ 
	.gallery-item-info {
		padding: 15px;
	}
	.item-info-right i {
	    width: 35px;
	    height: 35px;
	}
	.awesome {
	    font-size: 15px;
	}
	.llustration {
	    font-size: 13px;
	}
	/*---------- footer ----------*/
	.footer-logo,
	.copyright {
		text-align: center;
	}
	.footer-logo,
	.footer-top-list {
		margin-bottom: 15px;
	}
	.footer-list a {
	    line-height: 26px;
	}
	.adress-list .footer-list-item {
		margin-bottom: 5px;
	}

	.footer-list.adress-list {
		max-width: 240px;
		width: 100%;
		// margin: 0 auto;
		text-align: left;
	}
}

/* 576 - 768 px */
@media screen and (max-width: 768px) {
	/*---------- base ----------*/
	h1 {
		font-size: 47px;
	}

	/*---------- home ----------*/
	.wrap-home-offer {
		padding: 80px 0;
		.homeDescription{
			font-size: 17px;
			line-height: 21px;
		}
	}

	/*---------- what-we-do ----------*/
	.what-we-do {
	    padding-top: 75px;
	}
	.what-we-do-wrap {
	    margin-bottom: 75px;
	}
	/*---------- .our-services ----------*/
	.our-services {
		margin-top: 75px;
	}
	.our-services .title-block,
	.wrap-our-services-description,
	 .our-services-description {
		text-align: center;
	}

	.our-services .row:first-child {
		margin-bottom: 50px;
	}
	/* .reverse-description .title {
	    padding-left: 0;
	} */
	
	/*---------- our-portfolio ----------*/ 
	.awesome {
	    font-size: 16px;
	}
	.llustration {
	    font-size: 14px;
	}

	/*---------- footer ----------*/
	.footer-list,
	.footer-title {
		text-align: center;
	}
	.footer-list a {
	    line-height: 16px;
	}
	.footer-top-list,
	.footer-bottom-list {
		margin-bottom: 10px;
	}
	
	.copyright {
	    line-height: 15px;
	    margin: 45px 0 25px;
	}
	.footer-list.adress-list {
		margin: 0 auto;
	}
	
}

/* 320 - 480 px */
@media screen and (max-width: 480px) {
	/*---------- base ----------*/
	h1 {
		font-size: 30px;
	}
	h2,
	.get-started .title-block .title {
		font-size: 28px;
    	line-height: 1;
    	letter-spacing: 0;
	}
	h4 {
		// font-size: 30px;
		line-height: 1.2;
		margin-bottom: 10px;
		text-align: center;
	}
	.get-started .title-block .title {
		margin-bottom: 25px;
	}
	.sub-title {
		font-size: 12px;
	}
	.custom-btn {
		padding: 10px 20px;
		font-size: 12px;
	}
	.get-started-btn:first-child {
	    margin-right: 5px;
	}
	.mobile-none {
	  display: none;
	}
	/*---------- home ----------*/
	.wrap-home-offer {
		.homeDescription {
			font-size: 15px;
		}
	}
	.wrap-home-offer .home-btn i {
	    margin-left: 15px;
	}

	/*---------- our-portfolio ----------*/ 
	.gallery-item-info {
		height: 100%;
		// bottom: -203px;
	}

	.gallery-item-info {
		justify-content: center;
	}

	.item-info-right i {
	    width: 45px;
	    height: 45px;
	}
	/*---------- what-we-do ----------*/
	.what-we-do {
	    padding-top: 50px;
	}
	.what-we-do-wrap {
	    margin-bottom: 50px;
	}
	.what-we-do-form {
		padding: 15px;
	}
	.wrap-subscirbe input[type=email] {
		margin-bottom: 5px;
		padding: 10px 20px;
		text-align: center;
	}
	.wrap-subscirbe input[type=submit] {
		position: relative;
		top: 0;
    	right: 0;
    	padding: 11px 20px;
    	width: 100%;
    	text-align: center;
	}

	/*---------- testimonials ----------*/
	.testimonials .title-block {
	    margin-bottom: 60px;
	}
	/*---------- partners ----------*/
	.partners {
	    padding: 50px 0;
	}
	.get-started {
		padding: 70px 0;
	}
	
}
