.get-started  {
	background: linear-gradient(135deg, rgba(183,58,250,1) 20%, rgba(35,98,244,1) 100%);
	padding: 75px 0 100px;
}

.get-started .title-block {
	text-align: center;
}

.get-started,
.get-started .title-block .sub-title,
.get-started .title-block .title {
	color: $white;
}

.get-started .title-block .title {
	font-size: 50px;
	margin-bottom: 20px;
	letter-spacing: -2px;
}

.get-started-description {
   	margin-bottom: 55px;
}

.get-started-btn {
	&:first-child {
		margin-right: 35px;
	}
}

