.home {
	// min-height: 100vh;
	display: flex;
    align-items: center;
	&:before {
    content: '';
    // background: url('../img/main-page/main/main-bg/header-bg.png') no-repeat right top,
    background: url('../img/main-page/main/main-bg/bg.png') no-repeat center top,
    url('../img/main-page/main/main-bg/header-bg-left.png') no-repeat 0 900px;
    position: absolute;
    top: 0;
    // width: calc(50% + (0.94 * 1170px));
    width: 100%;
    height: 100%;
    z-index: -1;
  }
}

.wrap-home-offer {
	margin: 100px 0 300px;
	.home-title {
		padding-right: 350px;
		color: $white;
	}
	.homeDescription {
		color: $white;
		font-size: 23px;
		line-height: 46px;
		padding-right: 500px;
	}
	.home-btn i {
		margin-left: 25px;
	}
}

.mobile-home {
	display: flex;
    justify-content: center;
    margin: 50px auto 0;
}

/*---------- custom-btn ----------*/
.custom-btn {
	font-family: 'Montserrat', sans-serif;
	text-transform: uppercase;
	font-size: 13px;
	font-weight: 600;
	color: #fff;
	background: $custom-btn-bg;
	border: none;
	border-radius: 50px;
	box-shadow: 10px 7px 25px 1px rgba(185,185,185,0.5);
	padding: 20px 40px;
	cursor: pointer;
	transition: all .3s;
		&:hover {
		box-shadow: 4px 4px 9px 0 rgba(185, 185, 185, 1);
		}
}

.custom-btn.reverse {
	background: transparent;
    border: 2px solid #fff;
    box-shadow: none;
    &:hover {
		box-shadow: 4px 4px 9px 0 rgba(0, 0, 0, .3);
		color: $title-color;
		background: $white;
	}
}

/*---------- end custom-btn ----------*/